// Generated by grunt-webfont



@font-face {
    font-family:"qcon";
    src:url("../fonts/qcon-beta.eot");
    font-weight:normal;
    font-style:normal;
}
@font-face {
	font-family:"qcon";
	src:url("../fonts/qcon-beta.eot");
	src:url("../fonts/qcon-beta.eot?#iefix") format("embedded-opentype"),
		url("../fonts/qcon-beta.woff") format("woff"),
		url("../fonts/qcon-beta.ttf") format("truetype"),
		url("../fonts/qcon-beta.svg?#qcon") format("svg");
	font-weight:normal;
	font-style:normal;
}

.qcon {
	font-family:"qcon";
	display:inline-block;
	vertical-align:middle;
	line-height:1;
	font-weight:normal;
	font-style:normal;
	speak:none;
	text-decoration:inherit;
	text-transform:none;
	text-rendering:optimizeLegibility;
	-webkit-font-smoothing:antialiased;
	-moz-osx-font-smoothing:grayscale;
}


